.heading-blog{
  display: flex;
  flex-direction: column;
  align-items: center;
  h2{
    font-size: 52px;
    line-height: 60px;
  }
}

@media screen and (max-width: 1300px) {
  .heading-blog{
    h2{
      font-size: 46px;
    }
  }
}

@media screen and (max-width: 900px) {
  .heading-blog{
    h2{
      font-size: 35px;
    }
  }
}

@media screen and (max-width: 450px) {
  .heading-blog{
    h2{
      font-size: 30px;
      line-height: 115.61%;
      padding-bottom: 40px;
    }
  }
}